<template>
<div v-if="this.secName">
        <div class="topspan">
            <img src="../../../../assets/images/biaoti.png"/>
            <span>{{this.firName}} >> {{this.secName}}</span>
        </div>
        <div class="register-l" >
        <el-form
            :model="LoginForm"
            status-icon
            :rules="rules2"
            ref="LoginForm"
            class="demo-ruleForm"
            > 
            <el-form-item label="用戶名：" prop="userName" label-width="120px">
                <el-input v-model="LoginForm.userName" auto-complete="off" disabled=""></el-input>
            </el-form-item>
            <el-form-item prop="pswOld" label="原密碼：" label-width="120px">
                <el-input v-model="LoginForm.pswOld" auto-complete="off" placeholder="原密碼"></el-input>
            </el-form-item>
            <el-form-item prop="userPwd" label="新密碼：" label-width="120px">
                <el-input type="password" v-model="LoginForm.userPwd" auto-complete="off" placeholder="输入密碼"></el-input>
            </el-form-item>
            <el-form-item prop="checkPass" label="确认密碼：" label-width="120px">
                <el-input type="password" v-model="LoginForm.checkPass" auto-complete="off" placeholder="确认密碼"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click = "submitModify('LoginForm')" style="width:22%;margin-left: 11vw;">保存</el-button>
                <el-button type="primary" @click = "gotoLogin" style="width:20%;">返回</el-button>
            </el-form-item>
        </el-form>
        </div>

</div>
</template>
<script> 

export default {
  name: "Register", 
  data() {
    // <!--验证密码-->
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"))
      } else {
        if (this.LoginForm.checkPass !== "") {
          this.$refs.LoginForm.validateField("checkPass");
        }
        callback()
      }
    }
    // <!--二次验证密码-->
    let validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("請再次输入密碼"));
      } else if (value !== this.LoginForm.userPwd) {
        callback(new Error("两次输入密碼不一致!"));
      } else {
        callback(); 
      }
    };
    return {
        firName:localStorage.getItem('FIRNAME'),
        secName:localStorage.getItem('SECNAME'), 
        LoginForm: {
            pswOld: "",
            userPwd: "",
            userCode:"",
            userName:localStorage.getItem('USERNAME'),
        },
        rules2: {
            userPwd: [{ required: true,validator: validatePass, trigger: 'change' }],
            checkPass: [{ required: true,validator: validatePass2, trigger: 'change' }],
            pswOld: [{ required: true, message: '請輸入原密碼' }],
        },
    }
  }, 
  computed: {
    // userInfo() {
    //     return this.$store.state.userInfo;
    // },
  },
  methods: {
    // <!--提交修改-->
    submitModify(formName) {
        let self = this;
        this.$refs[formName].validate(valid => {
        if (valid) {
            //先判斷原密碼是否正確
            this.$store.dispatch('premodifyPwd',{"userCode":localStorage.getItem('USERCODE'), "userPwd":self.LoginForm.pswOld,
            }).then((rep) => {
                if(rep.data.code == 0){
                    //再提交修改密碼
                    this.$store.dispatch('submitModify',{"userCode":localStorage.getItem('USERCODE'), "userPwd":self.LoginForm.userPwd,
                        }).then((rep) => {
                      if(rep.data.code == 0){
                            // setTimeout(() => {
                                alert('修改成功')
                            // }, 400);
                            this.LoginForm = {
                                userName:localStorage.getItem('USERNAME'),
                                pswOld: "",
                                userPwd: "",
                                checkPass:"",
                            };
                        }else {
                            alert(rep.data.message)
                            return false;
                        }
                    });
                }else{
                    alert('原密碼錯誤')
                    this.LoginForm = {
                        userName:localStorage.getItem('USERNAME'),
                        pswOld: "",
                        userPwd: "",
                        checkPass:"",
                    };
                }

            }); 
          
        } else {
          console.log("error submit!!");
          return false;
        }
      })
    },
    // <!--返回-->
    gotoLogin() {
      this.$router.go(-1);
    },
   
  },
  mounted() {
        // this.userCode = this.userInfo.data;
    },
};
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";

.el-input{
    width: 100%;
    // float:left;
}
.register-l{
    float: left;
    width: 38%;
    margin: 10px 10px 0 18vw;
}

</style>


