<template>
  <div v-if="this.secName">
        <div class="topspan">
            <img src="../../../../assets/images/biaoti.png"/>
            <span>{{this.firName}} >> {{this.secName}}</span>
        </div>
        <div class="register authority">
            <div class="management">
                <span>用戶名:</span>
                <el-input v-model="LoginForm.userCode" auto-complete="off"  disabled=""></el-input>
            </div>
            <div class="management">
                <span>姓名:</span>
                <el-input v-model="LoginForm.userName"></el-input>
            </div>
            <div class="management">
                <span>手機號:</span>
                <el-input v-model="LoginForm.phoneNumber"></el-input>
            </div>
            <div class="management">
                <span>郵箱:</span>
                <el-input v-model="LoginForm.emailAddress"></el-input>
            </div>
            <div class="management">
                <span>性別:</span>
                <el-select  v-model="LoginForm.gender"  placeholder="请选择性別" class="gender-select">
                    <el-option
                        v-for="item in gender"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <!-- <div class="management">
                <span>地區:</span>
                <el-select  v-model="LoginForm.aids"  placeholder="请选择地區" class="gender-select">
                    <el-option
                        v-for="item in aid"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </div> -->
            <div class="management">
                <button class="btn-search btn-save" @click = "modifyUserMsg" >保存</button>
                <button class="btn-search" @click = "gotoLogin" >返回</button> 
            </div> 
        </div>
  </div>
</template>
<script> 

export default {
  name: "Register", 
  data() {
    
    return { 
        firName:localStorage.getItem('FIRNAME'),
        secName:localStorage.getItem('SECNAME'),
        LoginForm : {
            phoneNumber: "",
            userName: this.$route.params.data,
            userCode:this.$route.params.dataSecond,
            aids:[],
            gender: [],
            emailAddress: "",
        },
        gender: [
            {label: '男', value: 1 },
            {label: '女', value: 0 }
        ],
        // aid: [
        //     {label: '全國', value: 1 },
        //     {label: '華南', value: 4 },
        //     {label: '華北', value: 3 },
        //     {label: '華東', value: 2 },
            
        // ],
    }
  }, 
    created(){
    　　　　//select默认选中
        // this.aids = this.aid[0].value;
    },
    mounted () {
        this.searchUserMsg()
    },
  methods: {

    //查詢個人信息 
    searchUserMsg(){
        this.$store.dispatch('searchUserMsg',{"userCode":this.LoginForm.userCode})
        .then((rep) => {
        this.LoginForm = {
            userName: this.$route.params.data,
            userCode:this.$route.params.dataSecond,
            phoneNumber: rep.data.data.phoneNumber,
            // aids:rep.data.data.aid,
            emailAddress:rep.data.data.emailAddress,
            gender:rep.data.data.gender,
        };
        });
    },
    modifyUserMsg(){
        this.$store.dispatch('modifyUserMsg',{"userCode":this.$route.params.dataSecond,"userName":this.LoginForm.userName,"phoneNumber":this.LoginForm.phoneNumber,
        "emailAddress":this.LoginForm.emailAddress,"gender":this.LoginForm.gender
        // "aid":this.LoginForm.aids,
        })
            .then((rep) => {
                if(rep.data.code == 0){
                    alert("修改成功")
                    localStorage.setItem('USERNAME', rep.data.data);
                }else if(rep.code == 500){
                    // this.$message({
                    // message: '暫無此人信息',
                    // type: 'error'
                // })
            }  
        });
    },

    // <!--返回上一頁-->
    gotoLogin() {
    //   this.$router.push({
    //     path: "/personal/authority?firName=個人中心&secName=權限修改"
    //   });
        this.$router.go(-1);
    },

   
  }
};
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.authority{
    margin-left: 6vw;    
}
.management{
    padding: 30px ;
    text-align: center;
    width: 100%;
    span{
        width: 23%;
        font-size: 1.25em;
        float:left;
    }
    .el-input{
        width: 50%;
        float:left;
    }
    .el-select{
        float:left;
        width: 30%;
    }
    .btn-search{
        width: 5vw;
        line-height: 6vh;
        background-color: #4f71cf;
        color: #fff;
    }
    .btn-save{
        margin-right: 5vw;
        margin-left: -15vw;
    }
}
</style>


