<template>
  <div >
        <div class="register authority">
            <div class="management">
                <span class="span-style">用戶名:</span>
                <el-input v-model="this.$route.params.dataSecond" auto-complete="off"  disabled=""></el-input>
            </div>
            <div class="management">
                <span class="span-style">已有權限:</span>
                <el-input v-model="this.$route.params.permission" disabled=""></el-input>
            </div>
            <div class="management">
                <span class="span-style">修改權限為:</span>
                <!-- <el-input v-model="LoginForm.phoneNumber" disabled=""></el-input> -->
                <el-select  v-model="LoginForm.authorities"  placeholder="请选择修改權限"
                 class="gender-select" multiple v-if="this.$route.params.permId.length = 5">
                    <el-option
                        v-for="item in authoritym"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                <el-select  v-model="LoginForm.authorities"  placeholder="请选择修改權限"
                 class="gender-select" multiple v-if="this.$route.params.permId.length < 5">
                    <el-option
                        v-for="item in authority"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <div class="management switch">
                <span class="span-style">用戶狀態:</span>
                <el-switch
                v-model="LoginForm.status"
                class="demo"
                active-value="1"
                inactive-value="0"
                active-color="#00A854"
                inactive-color="#F04134" 
                active-text="启用"
                inactive-text="停用"
                @change="TE" >
                </el-switch>
            </div>
            <div class="management switch">
                <span class="span-style">用戶密碼重置:</span>
                <el-switch
                v-model="LoginForm.resetStatus"
                class="demo"
                active-value="1"
                inactive-value="0"
                active-color="#00A854"
                inactive-color="#F04134" 
                active-text="是"
                inactive-text="否"
                @change="TE" >
                </el-switch>
            </div>
            <div class="management">
                <button class="btn-search btn-save" @click = "savechange" >保存</button>
                <button class="btn-search" @click = "gotoLogin" >返回</button> 
            </div> 
        </div>
  </div>
</template>
<script> 

export default {
  name: "Register", 
  data() {
    
    return { 
        LoginForm: {
            authorities:[],
            status:this.$route.params.status,
            resetStatus:"0",
        },
        authoritym: [
            {label: '查询', value: 4 },
            {label: '修改', value: 3 },
            {label: '删除', value: 2 },
            {label: '增加', value: 1 },
            {label: '修改權限以及區域信息', value: 5 },//管理員
            // {label: '撤銷此權限', value: 6 },
        ],
        authority: [
            {label: '查询', value: 4 },
            {label: '修改', value: 3 },
            {label: '删除', value: 2 },
            {label: '增加', value: 1 }
        ]
         
    }
  }, 
   created(){

    },
  methods: {
      
    savechange(){
        let params = {       //入參格式處理
            // "euserInfo":this.$route.params.userCode,
            // "data":this.LoginForm.authorities,
            userInfo:{
                status:this.LoginForm.status || 0,
                userCode:this.$route.params.userCode
            },
            data:[],
            sign:this.LoginForm.resetStatus || 0
        }
        // let statusInfo = {
        //     status:this.LoginForm.status,
        //     resetStatus:this.LoginForm.resetStatus
        // }
        // localStorage.setItem('STATUSINFO', statusInfo);
        
        localStorage.setItem('STATUS', this.LoginForm.status);
        localStorage.setItem('RESETSTATUS', this.LoginForm.resetStatus);
        

        // params.userInfo.userCode = this.$route.params.userCode
        for (var j = 0; j < this.LoginForm.authorities.length; j++) {
                params.data.push ({
                    data:this.LoginForm.authorities[j],
                });
            }
        for(var i = 0; i < this.$route.params.permId.length-this.LoginForm.authorities.length; i++){          
            params.data.push ({
                data:6,        //撤銷此權限
            });
        }
        this.$store.dispatch('savechange',params)
            .then((rep) => {
            if(rep.data.code == 0){
                alert('修改成功')
                //更新已有權限
                this.$store.dispatch('searchUser',{"userCode":this.$route.params.userCode})
                    .then((rep) => { 
                    if(rep.code == 0){
                        this.$route.params.permission = [];
                        this.LoginForm = {
                            authorities:[],
                        }
                        for (var i = 0; i < rep.data.length; i++) {  
                            this.$route.params.permission.push(rep.data[i].permission);

                        }
                        let str = this.$route.params.permission.toString()
                        this.$route.params.permission = str

                        this.LoginForm.status = localStorage.getItem('STATUS')
                        this.LoginForm.resetStatus = localStorage.getItem('RESETSTATUS')
                    }
                
                });
                
                }else{
                    alert(rep.data.message)
                    // this.$message({
                    // message: '保存失敗',
                    // type: 'error'
                    // })
            }  
        });
    },
    mounted () {
        
    },
    // <!--返回上一頁-->
    gotoLogin() {
        this.$router.go(-1);
    },

   
  }
};
</script>
<style lang="scss" >
@import "../../../../assets/css/base-table.less";
.authority{
    margin-left: 6vw;    
}
.management{
    padding: 30px ;
    text-align: center;
    width: 100%;
    .span-style{
        width: 23%;
        font-size: 1.25em;
        float:left;
    }
    .el-input{
        width: 50%;
        float:left;
    }
    .el-select{
        float:left;
        width: 50%;
        .el-input{
        width: 100%;
    }
        
    }
    .btn-search{
        width: 5vw;
        line-height: 6vh;
        background-color: #4f71cf;
        color: #fff;
    }
    .btn-save{
        margin-right: 5vw;
        margin-left: -15vw;
    }
}
.switch{
  display: flex;
  align-items: center;
}
</style>


