                <!-- <img src="../../../../assets/images/homeBackground.jpg"/> -->
            </div> 
        </div>   
    </div>    
</template>

<script>
    export default {
        data(){
            return{
                firName:localStorage.getItem('FIRNAME'),
            }

        },
        mounted () {
        
        },
        methods: {
        

        },
        mounted() {
            
        }
    }
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.register-l{
    // float: left;
    width: 80%;
    margin-left: 10%;
}
.homeBackground{
    height:80%;
    width:90%;
    background: url(../../../../assets/images/homeBackground.jpg) center no-repeat;
    background-size: cover;
    position:absolute;
    left:13vw;
    top:150px;
}
</style>



