import Vue from 'vue'
import Vuex from 'vuex'

import login from './login';
import common from './common';
import mutations from './mutations';

// import actions from
Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    login,
    mutations,
    common,
    
  },
  state: {
    token: null,
    ssoToken: '',
    
    mipId : '',
    deviceId  : '', // 设备id  手机的唯一标示
    deviceInfo: {},
    userInfo: {
      user:{
        userName:"",
        mobile:""
      },
    },
    toast: {
      isShow: false,
      config: {
        msg: '',
        width: '7.6em',
        time: 2000
      }
    }
  },
  // actions,
  // mutations
});



