<template>
  <div v-if="this.secName">
        <div class="topspan">
            <img src="../../../../assets/images/biaoti.png"/>
            <span>{{this.firName}} >> {{this.secName}}</span>
        </div>
        <div class="register authority">
            <div class="management">
                <span class="span-style">工號:</span>
                <el-input v-model="LoginForm.userCode" auto-complete="off" placeholder="请输入工號"></el-input>
                <button class="btn-search" @click = "searchUser">查詢</button>
            </div>
            <div v-if="tipStatus" class="alert-tip">暫無此人信息</div>
            <div class="management" style="margin-bottom:25px;">
                <span class="span-style">用戶名:</span>
                <el-input v-model="LoginForm.dataSecond" disabled></el-input>
            </div>
            <div class="management">
                <span class="span-style">現在權限:</span>
                <el-input v-model="LoginForm.permission" disabled></el-input>
                <button :class="[LoginForm.dataSecond ? 'btn-search' : 'btn-search-disabled']" @click = "modifyUser" :disabled="!LoginForm.dataSecond || !LoginForm.permId">修改</button>  
            </div>
            <div class="management switch">
                <span class="span-style">用戶狀態:</span>
                <el-switch
                v-model="LoginForm.status"
                class="demo"
                active-value="1"
                inactive-value="0"
                active-text="启用"
                inactive-text="停用"
                active-color="#00A854"
                inactive-color="#F04134" 
                disabled>
                </el-switch>
                
            </div>
            <!-- <div class="management switch">
                <span class="span-style">用戶密碼重置:</span>
                <el-switch
                v-model="LoginForm.resetStatus"
                class="demo"
                active-value="1"
                inactive-value="0"
                active-color="#00A854"
                inactive-color="#F04134" 
                active-text="是"
                inactive-text="否" disabled>
                </el-switch>
            </div> -->

        </div>
  </div>
</template>
<script> 
import AuthorityModify from './authority-modify'

export default {
  name: "Register", 
  data() {
    
    return { 
        firName:localStorage.getItem('FIRNAME'),
        secName:localStorage.getItem('SECNAME'),
        LoginForm: {
            permId: [],
            permission: [],
            dataSecond: "",
            userCode:"",
            status: "1",
            // resetStatus: "",
        },
        tipStatus:false
    }
  }, 
  methods: {
    searchUser(){
        this.LoginForm.permission = [];
        this.$store.dispatch('searchUser',{"userCode":this.LoginForm.userCode})
            .then((rep) => {
              if(rep.code == 0){
                    this.LoginForm.dataSecond = rep.dataSecond;
                    this.LoginForm.status = rep.dataThird;
                    if(this.LoginForm.status == 1){
                        this.LoginForm.status = "1";
                    }else{
                        this.LoginForm.status = "0";
                    }
                    
                    // this.LoginForm.resetStatus = rep.dataSecond;

                    for (var i = 0; i < rep.data.length; i++) {
                        this.LoginForm.permId.push(rep.data[i].pid) ;
                        this.LoginForm.permission.push(rep.data[i].permission) ;
                    }
                    this.tipStatus = false;
                }else {
                    this.tipStatus = true;
                    this.LoginForm.dataSecond = ""
            }  
        });
    },
    modifyUser(){
        // new this.$pageModal( AuthorityModify,{
        //         props: {
        //             title: '用戶注冊',
        //         },
                
        //     }, 
        
        // );
        this.$router.push({
            path: "/authorityModify",
            name: "authorityModify",
            params: {
                dataSecond: this.LoginForm.dataSecond,
                permId: this.LoginForm.permId,
                permission: this.LoginForm.permission,
                userCode:this.LoginForm.userCode,
                status:this.LoginForm.status,
            }
        });
    },
    // <!--返回首頁-->
    gotoLogin() {
      this.$router.push({
        path: "/"
      });
    },

  }
};
</script>
<style lang="scss">
@import "../../../../assets/css/base-table.less";

.management{
    padding: 25px ;
    text-align: center;
    width: 100%;
    .span-style{
        width: 25%;
        font-size: 1.25em;
        float:left;
    }
    .el-input{
        width: 50%;
        float:left;
    }
    .btn-search {
        width: 4vw;
        line-height: 5vh; 
        color: #fff;
        background-color: #4f71cf;    
    }
    .btn-search-disabled{
        width: 4vw;
        line-height: 5vh; 
        color: #fff;
        background-color: #ddd;
    }
}
.alert-tip{
    padding-left: 23vw;
    color:red;
    font-size: 1.125em;
}
.switch{
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

</style>


