/**
 * 登錄
 */
import { api } from './api';
import axios from 'axios';
import { configUrl } from '../config';

const actions = {

    //登录
    localLogin ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.localLogin,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param
        }).then((rep) => {
            return (function() {
                // var userInfo =  {
                //     userName:"",
                //     userCode:""
                // }
                var userName = rep.data.data;
                var userCode = rep.data.dataSecond;
                var limits = rep.data.dataThird;
                // console.log(limits)
                // var token = rep.headers['x-auth-token'] || '';
                let addFlag = false 
                let delFlag = false
                let updateFlag = false
                let viewFlag = false
                let powerFlag = false
                if (rep && rep.data.code == 0) {
                    // commit('USERNAME', userName);
                    // commit('USERCODE', userCode);
                    // localStorage.setItem('Token', token);
                    localStorage.setItem('USERNAME', userName);
                    localStorage.setItem('USERCODE', userCode);
                    // localStorage.setItem('LIMITS', limits);//權限控制
              
                    if(limits.length > 0){

                        for (let i = 0; i < limits.length; i++) {
                            if(limits[i] == 'add'){
                                addFlag = true
                            }else if(limits[i] == 'del'){
                                delFlag = true
                            }else if(limits[i] == 'update'){
                                updateFlag = true
                            }else if(limits[i] == 'view'){
                                viewFlag = true
                            }else if(limits[i] == 'power'){
                                powerFlag = true
                            }
                            localStorage.setItem('LIMITS', JSON.stringify({ADD:addFlag?'add':'',
                            DEL:delFlag?'del':'', UPDATE:updateFlag?'update':'',
                            VIEW:viewFlag?'view':'', POWER:powerFlag?'power':''}));
                        }
                    }else if(limits.length == 0){
                        localStorage.setItem('LIMITS', JSON.stringify({ADD:'',DEL:'', UPDATE:'',VIEW:'', POWER:''}));
                    }
                }
                return rep.data;
            })();
        });
        
      },
    //註冊
    loginForm ({commit, dispathc, state, rootState}, param) {
    //   console.log(param);
        return axios({
            url: api.loginForm,
            method: 'post',
            baseURL: configUrl.chenUrl,//zhouUrl   baseUrl shiUrl  chenUrl
            data: param
        }).then((rep) => {
            return rep;
        });
        
      },
    
}

export default actions;



