<template>
<div>
    <div class="wrap"> 
      <!-- <login-form class="right-inputbox">
      </login-form> -->
    </div>

    <div class="login-title">
        <h3>歡迎登錄合作夥伴管理系統</h3>
        <div class="user-info">
            <span class="userinfo">用戶名</span>
            <input type="text" v-model="userName" class="userinput" @keyup.enter = "handleLogin"/>
        </div>
        <div class="user-info">
            <span class="userinfo">&nbsp;密&nbsp;碼&nbsp;&nbsp;</span>
            <input type="password" v-model="userPasswd" class="userinput" @keyup.enter = "handleLogin"/>
        </div>
        <div class="user-info">
            <button class="btn btn-login" @click = "handleLogin" :loading="loading">登錄</button>
            <button class="btn btn-sign" @click = "handleSign">注冊</button>
        </div>

    </div>  
</div>
  
</template>
<script>
  import LoginForm from './login-form/index'
  export default {
    name: 'Login',
    components: {
      LoginForm,
    },
    data() {
      return {
        sendVal: false,//
        checked: false,
        formData: {
          username: '',
          password: ''
        }
      }
    },
    computed: {
      
    },
    
    mounted () {
     
    },
    methods: {
        handleLogin () {
        let self = this;
            this.$store.dispatch('localLogin',{"userCode":self.userName, "userPwd":self.userPasswd})
            .then((rep) => {
                if(rep.code == 0){
                    let userName = localStorage.getItem('USERNAME')
                    let userCode = localStorage.getItem('USERCODE')
                    //   this.$router.replace("/home");
                    this.$router.replace({
                        path: '/home',
                        name: 'home',
                        params: {
                            data: userName,
                            dataSecond: userCode,
                        }
                    })
                    }else if(rep.code == 503){
                        this.$alert('用户密碼被管理员重置需要修改密碼', '', {
                            confirmButtonText: '确定',
                            showClose: false
                            }).then(() => {
                            this.$router.replace({
                                path: '/psdReset',
                                name: 'psdReset',
                            })
                        })
                    }else if(rep.code == 500){
                        this.$message({
                        message: '账號或密碼錯誤',
                        type: 'error'
                    })
                    }else{
                        alert(rep.message)
                        // this.$message({
                        // message: '用户被管理员停用！',
                        // type: 'error'
                        // })
                }  
            });
        
        },
        handleSign() {
            // new this.$pageModal( LoginForm,{
            //     props: {
            //         title: '用戶注冊',
            //     }, 
            // }, 
            
            // );
            this.$router.push("/login");
        }
    }

  }
</script>
<style lang="scss" scoped>
  
.wrap{
    height:100vh;
    width:100%;
    filter: blur(1px);
    background: url(../../../assets/images/background.png) center no-repeat;
    background-size: cover;
    position:absolute;
    left:0;
    top:0;

}
.login-title {
    text-align:center;
    color:#000;
    height:30vh;
    line-height:30vh;
    position:absolute;
    left: 10px;
    right: 10px;
    h3{
        font-size:1.8em;
        color: rgb(93, 189, 189);
    }
    .user-info{
        height:16vh;
        line-height:16vh;
        text-align: center;
        .userinfo{
            margin-right: 10px;
            width:16%;
            font-size:1.4em;
            text-align:left;
            color: rgb(74, 148, 148);
        }
        .userinput{
            width:20vw;
        }
    }
    
    .btn{
        height: 7vh;
        // width: 88px;
        width: 5vw;
        line-height: 7vh;
        text-align: center;
        color: rgb(255, 255, 255);
        font-size: 1.2em;
        font-weight: bold;
        cursor: pointer;
        border-width: 0px;
      
    }
    .btn-login{
        // background-color: #f8ec2f;
        background-color: #3ea4fa;
        margin-right: 3vw;
        margin-left: 3vw;
    }
    .btn-sign{
        background-color: #3ea4fa;
    }
}

</style>




