<template>
<div class="wrapper"> 
    <div class="main-header">   
        <span class="">合作夥伴管理系統</span>
        <div class="user-header"  v-if="this.userName">
            <img src="../../../../assets/images/login.png" class="img-header"/>
            <span class="span-header">{{this.userName}}</span>
            <div class="edit" @click="gotoLogin">
                <img src="../../../../assets/images/edit.png" class="img-header"/>
                <span class="span-header">注銷登錄</span>
            </div>
        </div>
    </div>
  
     <div class="asideBox">
      <aside>
        <ul class="asideMenu">
          <li v-for="(item,index) in menuList">
            <div class="oneMenu" @click="showToggle(item,index)">
                <img v-bind:src="item.imgUrl" />
                <span>{{item.name}}</span>
            </div>
            <ul v-show="item.isSubShow">
                <li v-for="(subItem,index) in item.subItems" :key="index" v-show="subItem.show">
                    <div class="oneMenuChild " v-bind:class="{open : openStatus == index}" @click = "childGo(subItem,index,item)">{{subItem.name}}</div>
                </li>
            </ul>
          </li>
        </ul>
      </aside>
     </div>

      <div style="margin-top:90px;margin-left: 12vw;">
        <!-- <home-form></home-form> -->
        <router-view></router-view>
        
      </div>
  
</div>    
</template>

<script>
  import HomeForm from './home-form'

  export default {
    components: {
       HomeForm,
    },
    data(){
        return{
            userName : localStorage.getItem('USERNAME'),
            userCode : localStorage.getItem('USERCODE'),

            menuList:[
            {
                name:'首頁',
                imgUrl:require('../../../../assets/images/main.png'),
                isSubShow:false,
                subItems:[
                {
                    name:'首頁信息',
                    url:'/homeForm',
                    show:true,
                },]
            },
            {
                name:'客戶管理',
                imgUrl:require('../../../../assets/images/customer.png'),
                isSubShow:false,
                subItems:[
                {
                    name:'新增客戶信息',
                    url:'/addCustomer',
                    // show:true,
                    show:JSON.parse(localStorage.getItem('LIMITS')).ADD == 'add'? true : false,
                   
                },
                {
                    name:'查詢客戶信息',
                    url:'/selectCustomer',
                    // show:true,
                    show:JSON.parse(localStorage.getItem('LIMITS')).VIEW == 'view'? true : false,
                },
                {
                    name:'修改客戶信息',
                    url:'/modifyCustomer',
                    // show:true,
                    show:JSON.parse(localStorage.getItem('LIMITS')).UPDATE == 'update'? true : false,
                },
                {
                    name:'刪除客戶信息',
                    url:'/deleteCustomer',
                    // show:true,
                    show:JSON.parse(localStorage.getItem('LIMITS')).DEL == 'del'? true : false,
                }
                ]
            },
            {
                name:'個人中心',
                imgUrl:require('../../../../assets/images/personal-center.png'),
                isSubShow:false,
                subItems:[
                {
                    name:'修改個人信息',
                    url:'/personalModify',
                    show:true,
                },
                {
                    name:'修改密碼',
                    url:'/modifyPsw',
                    show:true,
                },
                {
                    name:'權限修改' ,         //管理員可見
                    url:'/personal/authority',
                    // show:true,
                    show:JSON.parse(localStorage.getItem('LIMITS')).POWER == 'power'? true : false,
                },
                ]
            },
            // {
            //     name:'消息中心',
            //     imgUrl:require('../../../../assets/images/news-center.png'),
            //     isSubShow:false
            // },
            ],
            
        }
         
    },
    mounted () {
        if(localStorage.getItem('FIRNAME') == null){
            // degugger
            this.childGo_1()
        }else if(localStorage.getItem('FIRNAME')){
            // debugger
            this.childGo_2()
            
        }
    },
    methods: {
        showToggle:function(item,ind){
            this.menuList.forEach(i => {
            if (i.isSubShow !== this.menuList[ind].isSubShow) {
                i.isSubShow = false;
            }
            
            });
            item.isSubShow = !item.isSubShow;
        },
        childGo(subItem,index,item){
            let firName = item.name
            let secName = subItem.name

            this.$router.push({path: subItem.url,name: subItem.name, params: {
                
                firName:firName,secName:secName,
                data:localStorage.getItem('USERNAME'),dataSecond:localStorage.getItem('USERCODE')
            }});
            this.openStatus = index;

            localStorage.setItem('URL',subItem.url) 
            localStorage.setItem('FIRNAME', firName);
            localStorage.setItem('SECNAME', secName);
        },
        childGo_1(){
            localStorage.setItem('FIRNAME', '首頁');
            this.$router.push({path:'/homeForm' ,name:'首頁信息' , params: {
                
                firName:'首頁',secName:'首頁信息',
                data:localStorage.getItem('USERNAME'),dataSecond:localStorage.getItem('USERCODE')
            }});
        },
        childGo_2(){
            this.$router.push({path:localStorage.getItem('URL') ,name:localStorage.getItem('SECNAME') , params: {
                
                firName:localStorage.getItem('FIRNAME'),secName:localStorage.getItem('SECNAME'),
                data:localStorage.getItem('USERNAME'),dataSecond:localStorage.getItem('USERCODE')
            }});
        },
            
        // <!--退出登录-->
        gotoLogin() {
            this.$router.push({
                path: "/"
            });
            localStorage.removeItem('FIRNAME')
            localStorage.removeItem('USERCODE')
            localStorage.removeItem('USERNAME')
            localStorage.removeItem('LIMITS')
            localStorage.removeItem('PARTNERNO')
            localStorage.removeItem('URL')
            localStorage.removeItem('SECNAME')
        },
      

    },
  }
</script>
<style lang="scss" scoped>

body {font-size: 62.5%!important; /* 10÷16=62.5% */}
@media only screen and (min-width: 481px){
    body {
        font-size: 94%!important; /* 15.04÷16=94% */
    }
}
@media only screen and (min-width: 561px){
    body {
        font-size: 109%!important; /* 17.44÷16=109% */
    }
}
@media only screen and (min-width: 641px){
    body {
        font-size: 125%!important; /* 20÷16=125% */
    }
}

.wrapper {
    height: 100%;
    /*position: relative;*/
    overflow-x: hidden;
    overflow-y: hidden;
}
.main-header {
    background-color: #3e506a;
    // background-color: rgb(84, 92, 100);
    border-bottom:1px solid #2f3f57;
    position: fixed;
    z-index: 9;
    height: 90px;
    width: 100vw;
    text-align: center;
    span{
      color: #fff;
      font-size: 2em;
      line-height: 90px;
      height: 90px;
    }
    .user-header{
        text-align: center;
        position: absolute;
        top: 0;
        right: 30px;
        .img-header{
            width:25px;
            height:25px;
        }
        .span-header{
            font-size: 1.25em;
        }
            
        .edit{
            display:inline;
            margin-left: 10px;
            &:hover .img-header{
                content: url('../../../../assets/images/edit-hovor.png');

            } 
            &:hover .span-header{
                color:#a5acbe;
            }
            
        }
    }

    
}
  $menuBackColor:#6c809c;
  $menuListH2:#4f627c;
  .asideBox{
    height: 100vh;
    width: 12vw;
    padding-top: 90px;
    float: left;
    position: fixed;
    aside{
      background: $menuBackColor;
      height: 100vh;
      // position: fixed;
      .asideMenu{
        padding:0;
        margin:0;
        .oneMenu{
          height: 50px;
          line-height: 50px;
          font-size: 1.5em;
          font-weight: normal;
          color: #ffffff;
          background: $menuListH2 url("") no-repeat 12vw center;
          border-bottom: 2px solid #3d4e68;
        //   transform: rotate(135deg);
          img{
            width: 20px;
            height: 20px;
            margin: 15px 8px 15px 20px;
            vertical-align: top;
          }
        }
        // .oneMenu:active{
        //     background: #f8ec2f ;
        // }
        .oneMenuChild{
            padding: 0 20px 0 45px;
            height: 40px;
            line-height: 40px;
            background: $menuBackColor;
            border-bottom: 2px solid #7a8899;
        //   color: #454343;
            color:white;
            font-size: 1.25em;
        }
        .oneMenuChild:hover{
            background: #9eacbe;
        }
        .open{
           background: #9eacbe; 
        }
      }
    }
    ul,li{
      list-style:none;
      padding: 0;
      margin: 0;
    }
  }
  

</style>



