                            <el-col :span="12">
                                <el-form-item label="合作階段" label-width="130px" >
                                    <el-input v-model.trim="FormModel.ecooperationpartnerCostage" :disabled="ifMotify" ></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="是否有簽署合約" label-width="130px">
                                    <el-select  v-model="FormModel.ecooperationsignContract"  placeholder="請选择是否有簽署合約" class="gender-select">
                                        <el-option
                                            v-for="item in signContract"
