                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="是否有委托簽署關係" label-width="130px">
                                    <el-select  v-model="FormModel.ecooperationentrust"  placeholder="請选择是否有委托簽署關係" class="gender-select">
                                        <el-option
                                            v-for="item in entrust"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="合約委托方" label-width="130px" >
                                    <el-input v-model.trim="FormModel.ecooperationentrustName" :disabled="ifMotify" ></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="是否有授牌" label-width="130px">
                                    <el-select  v-model="FormModel.ecooperationpartnerAwarding"  placeholder="請选择是否有授牌" class="gender-select">
                                        <el-option
                                            v-for="item in partnerAwarding"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="合作項目名稱 " label-width="130px" >
                                    <el-input v-model.trim="FormModel.ecooperationprojectName" :disabled="ifMotify" ></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="合作業務類型" label-width="130px" >
                                    <el-input v-model.trim="FormModel.ecooperationcoType" :disabled="ifMotify" ></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="合作項目進度" label-width="130px" >
                                    <el-input v-model.trim="FormModel.ecooperationcoProgress" :disabled="ifMotify" ></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="Fii合作部門" label-width="130px" >
                                    <el-input v-model.trim="FormModel.ecooperationfiiCodepartment" :disabled="ifMotify" ></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                            <el-form-item label="附件" label-width="130px" >
                                <el-upload
                                ref="upload"
                                :action="`${BASE_URL}/tsbg/upload/import`"
                                :headers="headers"
                                :file-list="fileList"
                                :data="otherData"
                                :on-remove="fileRemvoeFunc"
                                :on-error="fileErrorFunc"
                                :before-upload="beforeUploadFunc"
                                :on-change="handleUploadChange"
                                :on-success="uploadCtrlFunc"
                                :on-exceed="handleExceed"
                                :limit="15 - this.fileLists.length" 
                                multiple
                                show-file-list="true"
                                accept=".pdf, .doc, .docx, .xlsx, .xls, .xlsm, .ppt, .pptx"
                                
                                :auto-upload="false"
                                :http-request="uploadSectionFile">
                                
                                    <el-button slot="trigger" type="plain" style="height:33px;font-size:1em;">
                                        選擇文件
                                    </el-button>
                                    <div slot="tip" class="el-upload__tip">只能上傳pdf、word、excel、ppt、txt文件，單次上傳附件大小不能超過30M。</div>
                                    
                                </el-upload>
                                <template v-for="(file, id) in this.fileLists" >
                                        <div class="flex-space-between file-item" :key="id">
                                            <div>
                                                <i class="el-icon-document"></i>
                                                    <a class="icon" :href="getDownload(file)" @click="download(file);" >{{file}}</a>
                                                <i class="el-icon-close closeFile" @click.stop="closeFile(file);"></i>
                                            </div>
                                        </div>
                                    </template>
                            </el-form-item>
                        </el-col>
                        </el-row>
                    </el-collapse-item>
                    <!-- 合作情況信息end -->
                    <!-- 底部功能鍵 -->
                    <el-form-item style="text-align:center;margin-top:25px;">
                        <!-- <el-button type="primary" @click = "saveCompany('FormModel')">保存</el-button> -->
                        <el-button type="primary" @click = "updateCompany('FormModel')" :disabled="isDisable" >提交</el-button>
                        <!-- <el-button type="primary" @click = "gotoLogin">返回</el-button> -->
                    </el-form-item>
                    
                </el-form> 
                </el-collapse>  
            </div>
