<template>
    <div>
        <div class="register register-l">
            <el-form
            :model="LoginForm"
            status-icon
            :rules="rules2"
            ref="LoginForm"
            class="demo-ruleForm"
            > 
            <el-form-item  label="工號:" prop="userCode" label-width="120px">
                <el-input v-model="LoginForm.userCode" auto-complete="off" placeholder="请输入工號"></el-input>
            </el-form-item>

            <el-form-item prop="userPwd" label="新密碼：" label-width="120px">
                <el-input type="password" v-model="LoginForm.userPwd" auto-complete="off" placeholder="输入密碼"></el-input>
            </el-form-item>
            <el-form-item prop="checkPass" label="确认密碼：" label-width="120px">
                <el-input type="password" v-model="LoginForm.checkPass" auto-complete="off" placeholder="确认密碼"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click = "submitModify('LoginForm')" style="width:20%;margin-left: 12vw;">提交</el-button>
                <el-button type="primary" @click = "gotoLogin" style="width:20%;">返回</el-button>
            </el-form-item>
            </el-form>
        </div>
  </div>
</template>
<script> 

export default {
  name: "Register", 
  data() {
    // <!--验证密码-->
    let validatePass = (rule, value, callback) => {
        if (value === "") {
            callback(new Error("请输入密码"))
        } else {
            if (this.LoginForm.checkPass !== "") {
            this.$refs.LoginForm.validateField("checkPass");
            }
            callback()
        }
    }
    // <!--二次验证密码-->
    let validatePass2 = (rule, value, callback) => {
        if (value === "") {
            callback(new Error("請再次输入密碼"));
        } else if (value !== this.LoginForm.userPwd) {
            callback(new Error("两次输入密碼不一致!"));
        } else {
            callback(); 
        }
    };
    return { 
        LoginForm: {
            userPwd: "",
            userCode:"",
            checkPass:"",
        },
        rules2: {
            userCode: [{ required: true, message: '請輸入工號' }],
            userPwd: [{ required: true,validator: validatePass, trigger: 'change' }],
            checkPass: [{ required: true,validator: validatePass2, trigger: 'change' }],
        },
    }
  }, 
  methods: {
     submitModify(formName) {
        let self = this;
        this.$refs[formName].validate(valid => {
        if (valid) {
            this.$store.dispatch('submitModify',{"userCode":self.LoginForm.userCode, "userPwd":self.LoginForm.userPwd,
                }).then((rep) => {
                if(rep.data.code == 0){
                    alert('修改成功,修改后密码为'+rep.data.data.userPwd)
                    this.LoginForm = {
                        userPwd: "",
                        checkPass:"",
                    };
                }else {
                    alert(rep.data.message)
                    return false;
                }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      })
    },
    // <!--返回首頁-->
    gotoLogin() {
      this.$router.push({
        path: "/"
      });
    },

  }
};
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.register-l{
    float: left;
    width: 38%;
    margin: 10px 10px 0 18vw;
}
.btn-search {
    width: 4vw;
    // line-height: 5vh;
    line-height: 25px;
    color: #fff;
    background-color: #4f71cf;    
}

.el-input{
    width: 80%;
}
</style>


