                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="電話號碼" prop="phoneNumber" label-width="130px" >
                                <el-input v-model.trim="FormModel.phoneNumber" disabled></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="郵箱" prop="emailAddress" label-width="130px" >
                                <el-input v-model.trim="FormModel.emailAddress" disabled></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="所屬公司名稱" label-width="130px" >
                                <el-input v-model.trim="FormModel.eccontactspartnerName" disabled></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- 公司聯係人信息end -->
                <!-- 合作情況信息start -->
                <el-collapse-item title="合作情況信息" name="3">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="合作夥伴公司名稱" label-width="130px" >
                                <el-input v-model.trim="FormModel.ecooperationPartnerName" disabled></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="Callin時間" prop="partnerCallintime" label-width="130px" >
                                <el-date-picker
                                type="date"
                                v-model="FormModel.partnerCallintime"
                                style="width: 100%;"
                                :editable="true"
                                :picker-options="birthOptions"
                                @change="getSTime"
                                disabled>
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="Callin BD Owner" prop="partnerBdOwner" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerBdOwner" disabled></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合作階段" prop="partnerCostage" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerCostage" disabled></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="是否有簽署合約" prop="signContract" label-width="130px">
                                <el-select  v-model="FormModel.signContract" class="gender-select" disabled>
                                    <el-option
                                        v-for="item in signContract"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合約起始日" label-width="130px" >
                                <el-date-picker
                                    type="date"
                                    v-model="FormModel.contractDate"
                                    style="width: 100%;"
                                    :editable="true"
                                    :picker-options="birthOptions"
                                    @change="getSTime"
                                    disabled>
                                    </el-date-picker>
                                </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="是否有委托簽署關係" label-width="130px">
                                <el-select  v-model="FormModel.entrust" class="gender-select" disabled>
                                    <el-option
                                        v-for="item in entrust"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合約委托方" label-width="130px" >
