                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="是否有授牌" label-width="130px">
                                <el-select  v-model="FormModel.partnerAwarding" class="gender-select" disabled>
