                                        v-for="item in partnerAwarding"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合作項目名稱" label-width="130px" >
                                <el-input v-model.trim="FormModel.projectName" disabled></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="合作業務類型" label-width="130px" >
                                <el-input v-model.trim="FormModel.coType" disabled></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合作項目進度" label-width="130px" >
                                <el-input v-model.trim="FormModel.coProgress" disabled></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="Fii合作部門" label-width="130px" >
                                <el-input v-model.trim="FormModel.fiiCodepartment" disabled></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                        <el-form-item
                            label="附件"
                            prop="dataThird"
                            label-width="130px"
                            >
                            <template v-for="(file, id) in this.fileList" >
                                <div class="flex-space-between file-item" :key="id">
                                <div>
                                    <i class="el-icon-document"></i>
                                    <a class="icon" :href="getDownload(file)" @click="download(file);" >{{file}}</a>
                                </div>
                                </div>
                            </template>
                        </el-form-item>
                        </el-col>
                        <!-- <div v-if="FormModel.fiiCodepartment">
                            <el-col :span="12">
                                <h3>暂无附件</h3>
                            </el-col>
                        </div> -->
                         
                    </el-row>
                </el-collapse-item>
                <!-- 合作情況信息end -->
                <el-collapse-item title="编辑情況" name="4">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="添加人:" label-width="130px" >
                                <el-input v-model.trim="FormModel.creater" disabled></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="添加時間" label-width="130px" >
                                <el-date-picker
                                type="date"
                                v-model="FormModel.createTime"
                                style="width: 100%;"
