                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="最后编辑時間" label-width="130px" >
                                <el-date-picker
                                type="date"
                                v-model="FormModel.updateTime"
                                style="width: 100%;"
                                :editable="false"
                                :picker-options="birthOptions"
