</template>
<script>
import { BASE_URL } from '@/store/api'
export default {
    data(){
      return {
            partnerNo:localStorage.getItem('PARTNERNO'),
            activeNames: ['1'],
            // 绑定数据
            FormModel: {
            },
            gender: [
                {label: '男', value: 1 },
                {label: '女', value: 0 },
                {label: '未知', value: null },
            ],
            partnerList: [                //是否为上市公司
                {label: '是', value: 1 },
                {label: '否', value: 0 }
            ],
            signContract: [                //是否有签署合约
                {label: '是', value: 1 },
                {label: '否', value: 0 }
            ],
            entrust: [                     //是否有委托签署关系
                {label: '是', value: 1 },
                {label: '否', value: 0 }
            ], 
            partnerAwarding: [             //是否有授牌
                {label: '是', value: 1 },
                {label: '否', value: 0 }
            ], 
            fileList:null
        }
    },
    components: {
    //   Home
    },
    mounted () {
        this.open()
        this.filedetail()
    },
    methods: {
    //查询合作伙伴信息詳情
    open(){
        this.$store.dispatch('getEcooperationList', {partnerNo:this.partnerNo})
        .then(res => {
            if(res.data.code == 0){
                let dataContet = res.data.data[0]
                this.FormModel = {
                    // ecooperation:{
                        ecooperationPartnerName: dataContet.ecooperation ? dataContet.ecooperation[0].partnerName:'',
                        partnerCallintime: dataContet.ecooperation ? dataContet.ecooperation[0].partnerCallintime:'',
                        partnerBdOwner: dataContet.ecooperation ? dataContet.ecooperation[0].partnerBdOwner:'',
                        partnerCostage: dataContet.ecooperation ? dataContet.ecooperation[0].partnerCostage:'',
                        signContract: dataContet.ecooperation ? dataContet.ecooperation[0].signContract:'',
                        contractDate: dataContet.ecooperation ? dataContet.ecooperation[0].contractDate:'',
                        entrust: dataContet.ecooperation ? dataContet.ecooperation[0].entrust:'',
                        entrustName: dataContet.ecooperation ? dataContet.ecooperation[0].entrustName:'',
                        partnerAwarding: dataContet.ecooperation ? dataContet.ecooperation[0].partnerAwarding:'',
                        projectName: dataContet.ecooperation ? dataContet.ecooperation[0].projectName:'',
                        coType: dataContet.ecooperation ? dataContet.ecooperation[0].coType:'',
                        coProgress: dataContet.ecooperation ? dataContet.ecooperation[0].coProgress:'',
                        fiiCodepartment: dataContet.ecooperation ? dataContet.ecooperation[0].fiiCodepartment:'',

                        // },
                    // epartner : {
                        epartnerpartnerName: dataContet.epartner ? dataContet.epartner.partnerName:'',
                        partnerIndustry: dataContet.epartner ? dataContet.epartner.partnerIndustry:'',
                        partnerRegion: dataContet.epartner ? dataContet.epartner.partnerRegion:'',
                        partnerProduct: dataContet.epartner ? dataContet.epartner.partnerProduct:'',
                        partnerList: dataContet.epartner ? dataContet.epartner.partnerList:'',
                        partnerScale: dataContet.epartner ? dataContet.epartner.partnerScale:'',
                        partnerAddr: dataContet.epartner ? dataContet.epartner.partnerAddr:'',
                        partnerWebddr: dataContet.epartner ? dataContet.epartner.partnerWebddr:'',
                        partnerTech: dataContet.epartner ? dataContet.epartner.partnerTech:'',
                        partnerDate: dataContet.epartner ? dataContet.epartner.partnerDate:'',
                        partnerChannel: dataContet.epartner ? dataContet.epartner.partnerChannel:'',
                        partnerTurnover: dataContet.epartner ? dataContet.epartner.partnerTurnover:'',
                        partnerRegistcapital: dataContet.epartner ? dataContet.epartner.partnerRegistcapital:'',

                        createTime: dataContet.epartner ? dataContet.epartner.createTime:'',
                        updateTime: dataContet.epartner ? dataContet.epartner.updateTime:'',
                        creater: dataContet.epartner ? dataContet.epartner.creater:'',
                        updater: dataContet.epartner ? dataContet.epartner.updater:'',
                    // },
                    // eccontacts: {
                        
                        name: dataContet.eccontacts ? dataContet.eccontacts[0].name:'',
                        gender: dataContet.eccontacts ? dataContet.eccontacts[0].gender:'',
                        phoneNumber: dataContet.eccontacts ? dataContet.eccontacts[0].phoneNumber:'',
                        eccontactspartnerName: dataContet.eccontacts ? dataContet.eccontacts[0].partnerName:'',
                        emailAddress: dataContet.eccontacts ? dataContet.eccontacts[0].emailAddress:'',
                        title: dataContet.eccontacts ? dataContet.eccontacts[0].title:'',
                    // },

                } ;
            }
        })
    },
    filedetail(){
        // this.$store.dispatch('filedetail', {partnerNo:this.partnerNo})
        this.$store.dispatch('filedetail', {partnerNo:this.partnerNo})
        .then(res => {
            if(res.code == 0){
                this.fileList = res.dataSecond
            }
        })
    },
    getDownload (file) {
        let param = {
            partnerNo:this.partnerNo,
            fileName:file
        }
        return `${BASE_URL}/tsbg/upload/testdownload?fileName=`+file+`&partnerNo=`+this.partnerNo
        //  return `http://127.0.0.1:80/tsbg/upload/testdownload?fileName=`+file+`&partnerNo=`+this.partnerNo
        // window.open(`http://127.0.0.1:80/tsbg/upload/testdownload`,{file:file.substring(file.lastIndexOf("\/")+1+9,file.length)})

    },
    download (file) {
        const form = document.createElement('form')
        const formConfig = {
            action: this.getDownload(file),
            method: 'get',
            // method: 'get',
            // data: file.substring(file.lastIndexOf("\/")+1+9,file.length).split('.')[0],
        }
        // console.log(formConfig)
        Object.entries(formConfig).map(
            ([key, val]) => form.setAttribute(key, val)
            )
        form.submit()
    },
    // <!--返回-->
    gotoLogin() {
        this.$router.go(-1);
    },
    }
}
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.register-l{
    float: left;
    width: 80%;
    margin-left: 10%;
}
.el-input{
    width: 100%;
}
.el-select{
    width: 100%;
}
.titleSpan{
    margin:30px 0;
    font-size: 1.5em;
    color:#030303;

}
</style>


