/**
 * 個人中心
 */
import { api } from './api';
import axios from 'axios';
import { configUrl } from '../config';

const actions = {

    //工號查詢用戶
    searchUser ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.searchUser,
            method: 'post',
            baseURL: configUrl.baseUrl,// baseUrl  chenUrl
            data: param
        }).then((rep) => {
          return (function() {  
            return rep.data;
          })();
        });
        
      },
    //查詢個人信息
    searchUserMsg ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.searchUserMsg,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
    },
    //权限修改
    savechange ({commit, dispathc, state, rootState}, param) {
    //   console.log(param);
        return axios({
            url: api.savechange,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
        //   console.log(rep)
            return rep;
        });
        
    },
     
    //判断原密码是否正确
    premodifyPwd ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.premodifyPwd,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //密碼修改
    submitModify ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.submitModify,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //获取查詢公司信息表格数据
    getCompanyData ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.getCompanyData,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //修改個人信息
    modifyUserMsg ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.modifyUserMsg,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //管理员隐藏公司
    hideCompany ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.hideCompany,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //管理员刪除公司
    delCompany ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.delCompany,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //合作伙伴信息、合作情况信息、公司联系人信息新增
    addCompany ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.addCompany,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param,
            // headers: {
            //     'Content-Type': 'multipart/form-data'
            // },
        }).then((rep) => {
            return rep;
        });
          
    },
    //查询合作伙伴信息詳情
    getEcooperationList ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.getEcooperationList,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param
        }).then((rep) => {
            return rep;
        });
          
    },
    //合作伙伴信息、合作情况信息、公司联系人信息修改
    updateCompany ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.updateCompany,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //合作伙伴信息、合作情况信息、公司联系人信息修改
    updateCompany2 ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.updateCompany2,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //http://127.0.0.1:8080/import
    upload ({commit, dispathc, state, rootState}, param) {
        // console.log(param);
        return axios({
            url: api.upload,
            method: 'post',
            baseURL: configUrl.baseUrl,
            data: param,
           
        }).then((rep) => {
            return rep;
        });
          
    },
    
    //export
    export ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.export,
            method: 'get',
            baseURL: configUrl.baseUrl,
            data: param,
            headers: {
                'Content-Type': 'multipart/form-data'
            },
            responseType: 'blob'
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //查詢文件列表
    filedetail ({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.filedetail,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param,
        }).then((rep) => {
            return rep.data;
        });
          
    },
    //
    testdownload({commit, dispathc, state, rootState}, param) {
        return axios({
            url: api.testdownload,
            method: 'post',
            baseURL: configUrl.chenUrl,
            data: param,
        }).then((rep) => {
            return rep.data;
        });
          
    },
}

export default actions;



