import Vue from 'vue'
import Router from 'vue-router'
// import { getInfo } from '@/utils/auth'
Vue.use(Router)

/** 子路由 */
// import cusRoutes from './customerManagement.js' // 客戶管理
// import employeeRoutes from './employee.js' //個人中心

// const Layout = () => import('@/components/common/layout/layout')
const Blank = () => import('@/components/common/layout/blank')
const Error404 = () => import('components/common/error/404')
/** 组件 */
import Admin from '../pages/home/home'
import homeForm from '../pages/home/home-form'
import Login from '@/components/common/login/index'
import login from '@/components/common/login/login-form/index'

import AddCustomer from '../pages/customer-management/addCustomer'
import SelectCustomer from '../pages/customer-management/selectCustomer'
import ModifyCustomer from '../pages/customer-management/modifyCustomer'
import DeleteCustomer from '../pages/customer-management/deleteCustomer'
import CustomerDetails from '../pages/customer-management/customerDetails'
import CustomerUpdate from '../pages/customer-management/customerUpdate'

import AuthorityManagement from '../pages/personal-center/authority-management'
import AuthorityModify from '../pages/personal-center/authority-modify'
import PersonalModify from '../pages/personal-center/personal-modify'
import ModifyPsw from '../pages/personal-center/modify-psw'
import PsdReset from '../pages/personal-center/psd-reset'

const router = new Router({
  scrollBehavior: () => ({y: 0}),
  routes: [
    {
        path: '/',
        name: '登录页',
        component: Login
    },
    {
        path: '/login',
        name: '登录页',
        component: login
    },
    {
      path: '/home',
      name: 'home',
      component: Admin,
      // redirect: "Admin",      
      children: [
        {
            path: '/home',
            name: '首頁',
            component: Blank,
            meta: {
                title: '',
                requireAuth: true,  
            },
            children: [
                {
                path: '/homeForm',
                name: '首頁信息',
                component: homeForm,
                meta: {
                    title: '',
                    requireAuth: true,  
                },
                }, 
            ],
        },
        {
          path: '/customer',
          name: '客戶管理',
          component: Blank,
          children: [
            {
                path: '/addCustomer',
                name: '新增客戶信息',
                component: AddCustomer,
                meta: {
                    title: '',
                    requireAuth: true,  
                },
            }, 
            {
                path: '/selectCustomer',
                name: '查詢客戶信息',
                component: SelectCustomer,
                meta: {
                    title: '',
                    requireAuth: true,  
                },
              
            }, 
            {
                path: '/modifyCustomer',
                name: '修改客戶信息',
                component: ModifyCustomer,
                meta: {
                    title: '',
                    requireAuth: true,  
                },
            }, 
            {
                path: '/deleteCustomer',
                name: '刪除客戶信息',
                component: DeleteCustomer,
                meta: {
                    title: '',
                    requireAuth: true,  
                },
            },

          ]
        }, 
        {
          path: '/personal',
          name: '個人中心',
          component: Blank,
          children: [
            {
                path: '/personalModify',
                name: '修改個人信息',
                component: PersonalModify,
                meta: {
                    title: '',
                    requireAuth: true,  
                },
            }, 
            {
                path: '/modifyPsw',
                name: '修改密碼',
                component: ModifyPsw,
                meta: {
                    title: '',
                    requireAuth: true,  
                },
              
            }, 
            {
                path: '/personal/authority',
                name: '權限修改',
                component: AuthorityManagement, 
                meta: {
                    title: '',
                    requireAuth: true,  
                },          
            },
          ]
        },
      ],
      
    },
    {
        path: '/authorityModify',
        name: "authorityModify",//权限修改管理
        component: AuthorityModify,
        meta: {
            title: '',
            requireAuth: true,  
        },
    },
    {
        path: '/psdReset',
        name: "psdReset",//密碼被重置后登錄要修改密碼
        component: PsdReset,
    },
    {
        path: '/customerDetails',
        name: "customerDetails",
        component: CustomerDetails,
        meta: {
            title: '',
            requireAuth: true,  
        },
    },
    {
      path: '/customerUpdate',
      name: "customerUpdate",
      component: CustomerUpdate,
      meta: {
        title: '',
        requireAuth: true,  
    },
    
  },
  {
    path: 'error_404',
    component: Error404
  },
  {
    path: '*',
    component: Error404
  },
    
  ]

})


// router.afterEach((to, from, next) => {
//   getInfo(to, from, next, true)
// })
export default router



