<template>    
    <div v-if="this.secName" style="overflow-y: auto;">
        <div class="topspan">
            <img src="../../../../assets/images/biaoti.png"/>
            <span>{{this.firName}} >> {{this.secName}}</span>
        </div>
        <div class="register-l">
            <div class="content">
                <el-collapse v-model="activeNames" @change="handleChange">

                <el-form :rules="rules" :model="FormModel" ref="FormModel">
                    <el-collapse-item title="合作夥伴信息" name="1">
                <!-- 合作夥伴信息start -->
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="合作夥伴公司名稱" prop="epartnerpartnerName" label-width="130px" >
                                <el-input v-model.trim="FormModel.epartnerpartnerName"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="行業" prop="partnerIndustry" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerIndustry"></el-input>                       
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="業務主要區域" prop="partnerRegion" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerRegion" ></el-input>                       
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="主營產品/業務/服務" prop="partnerProduct" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerProduct"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="是否爲上市公司" prop="partnerList" label-width="130px">
                                <el-select  v-model="FormModel.partnerList"  placeholder="請选择是否爲上市公司" class="gender-select">
                                    <el-option
                                        v-for="item in partnerList"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="公司規模" prop="partnerScale" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerScale"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="公司地址" prop="partnerAddr" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerAddr"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="公司網址" prop="partnerWebddr" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerWebddr" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="公司核心技術" prop="partnerTech" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerTech"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="公司注冊時間" prop="partnerDate" label-width="130px" >
                                <el-date-picker
                                type="date"
                                v-model="FormModel.partnerDate"
                                style="width: 100%;"
                                :editable="true"
                                :picker-options="birthOptions"
                                @change="getSTime">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="業務渠道" prop="partnerChannel" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerChannel" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="營業額" prop="partnerTurnover" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerTurnover" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="公司注冊資本" prop="partnerRegistcapital" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerRegistcapital" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- 合作夥伴信息end -->          
                <!-- 公司聯係人信息start -->
                <el-collapse-item title="聯係人信息" name="2">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="姓名" prop="name" label-width="130px" >
                                <el-input v-model.trim="FormModel.name" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="職稱" label-width="130px" >
                                <el-input v-model.trim="FormModel.title" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="性別" prop="gender" label-width="130px">
                                <el-select  v-model="FormModel.gender"  placeholder="請选择性別" class="gender-select">
                                    <el-option
                                        v-for="item in gender"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="電話號碼" prop="phoneNumber" label-width="130px" >
                                <el-input v-model.trim="FormModel.phoneNumber" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="郵箱" prop="emailAddress" label-width="130px" >
                                <el-input v-model.trim="FormModel.emailAddress" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="所屬公司名稱" prop="epartnerpartnerName" label-width="130px" >
                                <el-input v-model.trim="FormModel.epartnerpartnerName" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- 公司聯係人信息end -->
                <!-- 合作情況信息start -->
                <el-collapse-item title="合作情況信息" name="3">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="合作夥伴公司名稱" prop="epartnerpartnerName" label-width="130px" >
                                <el-input v-model.trim="FormModel.epartnerpartnerName" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="Callin時間" prop="partnerCallintime" label-width="130px" >
                                <el-date-picker
                                type="date"
                                v-model="FormModel.partnerCallintime"
                                style="width: 100%;"
                                :editable="true"
                                :picker-options="birthOptions"
                                @change="getSTime">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="Callin BD Owner" prop="partnerBdOwner" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerBdOwner" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合作階段" prop="partnerCostage" label-width="130px" >
                                <el-input v-model.trim="FormModel.partnerCostage" :disabled="ifMotify" placeholder="已Callin，已签署保密合约，已签署战略合作协议，已签署市场合作协议"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                     <el-row>
                        <el-col :span="12">
                            <el-form-item label="是否有簽署合約" prop="signContract" label-width="130px">
                                <el-select  v-model="FormModel.signContract"  placeholder="請选择是否有簽署合約" class="gender-select">
                                    <el-option
                                        v-for="item in signContract"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合約起始日" prop="contractDate" label-width="130px" >
                                <el-date-picker
                                    type="date"
                                    v-model="FormModel.contractDate"
                                    style="width: 100%;"
                                    :editable="true"
                                    :picker-options="birthOptions"
                                    @change="getSTime">
                                    </el-date-picker>
                                </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="是否有委托簽署關係" prop="entrust" label-width="130px">
                                <el-select  v-model="FormModel.entrust"  placeholder="請选择是否有委托簽署關係" class="gender-select">
                                    <el-option
                                        v-for="item in entrust"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合約委托方" prop="entrustName" label-width="130px" >
                                <el-input v-model.trim="FormModel.entrustName" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="是否有授牌" prop="partnerAwarding" label-width="130px">
                                <el-select  v-model="FormModel.partnerAwarding"  placeholder="請选择是否有授牌" class="gender-select">
                                    <el-option
                                        v-for="item in partnerAwarding"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合作項目名稱 " prop="projectName" label-width="130px" >
                                <el-input v-model.trim="FormModel.projectName" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="合作業務類型" prop="coType" label-width="130px" >
                                <el-input v-model.trim="FormModel.coType" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="合作項目進度" prop="coProgress" label-width="130px" >
                                <el-input v-model.trim="FormModel.coProgress" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="Fii合作部門" prop="fiiCodepartment" label-width="130px" >
                                <el-input v-model.trim="FormModel.fiiCodepartment" :disabled="ifMotify" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <!--    :action="`${$$.BASE_URL}${uploadApi}`"
                             :action="'http://127.0.0.1:8080/tsbg/company/addCompany
                             http://127.0.0.1:80/tsbg/upload/import'"
                                    -->
                            <el-form-item label="附件" label-width="130px">
                                <el-upload
                                ref="upload"
                                :action="`${BASE_URL}/tsbg/upload/importmore`"
                                :headers="headers"
                                :file-list="fileList"
                                :data="otherData"
                                :on-remove="fileRemvoeFunc"
                                :on-error="fileErrorFunc"
                                :before-upload="beforeUploadFunc"
                                :on-change="handleUploadChange"
                                :on-success="uploadCtrlFunc"
                                :on-exceed="handleExceed"
                                :limit="15" multiple
                                accept=".pdf, .doc, .docx, .xlsx, .xls, .xlsm, .ppt, .pptx"
                                
                                :auto-upload="false"
                                :http-request="uploadSectionFile">
                                <el-button slot="trigger" type="plain" style="height:33px;font-size:1em;">
                                    選擇文件
                                </el-button>
                                <div slot="tip" class="el-upload__tip">只能上傳pdf、word、excel、ppt、txt文件，單次上傳附件大小不能超過30M。</div>
                                <!-- <p>文件大小不能超过{{ fileLimitSize }}M</p> -->
                                <!-- <span
                                    slot="tip"
                                    v-if="tipFlag"
                                    class="el-upload__tip font-size-small ml10">
                                    未选择任何文件
                                </span> -->
                                </el-upload>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                    <!-- 合作情況信息end -->
                    <!-- 底部功能鍵 -->
                    <el-form-item style="text-align:center;margin-top:25px;">
                        <!-- <el-button type="primary" @click = "saveCompany('FormModel')">保存</el-button> -->
                        <el-button type="primary" @click = "addCompany('FormModel')" :disabled="isDisable">提交</el-button>
                        <el-button type="primary" @click = "gotoLogin">返回</el-button>
                    </el-form-item>
                </el-form> 
                </el-collapse>  
            </div>
        </div>
    </div> 
    
</template>
<script>
import { BASE_URL } from '@/store/api'
export default {
    data(){
    let checkTel = (rule, value, callback) => {
        if (value === '') {
            callback(new Error('請输入手机號碼'))
        }else if (!this.checkMobile(value)) {
            callback(new Error('手机號碼格式不正確'))
        }else {
            callback()
        }
    }
    return {
        firName:localStorage.getItem('FIRNAME'),
        secName:localStorage.getItem('SECNAME'),
        // 绑定数据
        FormModel: {
            partnerName: '',
            partnerIndustry: '',
            partnerRegion: '',
            partnerProduct: '',
            name: '',
            gender: '',
            phoneNumber: '',
        },
        activeNames: ['1','2','3'],
        gender: [
            {label: '男', value: 1 },
            {label: '女', value: 0 }
        ],
        partnerList: [                //是否为上市公司
            {label: '是', value: 1 },
            {label: '否', value: 0 }
        ],
        signContract: [                //是否有签署合约
            {label: '是', value: 1 },
            {label: '否', value: 0 }
        ],
        entrust: [                     //是否有委托签署关系
            {label: '是', value: 1 },
            {label: '否', value: 0 }
        ], 
        partnerAwarding: [             //是否有授牌
            {label: '是', value: 1 },
            {label: '否', value: 0 }
        ],
        // 表单的验证规则
        rules: {
            epartnerpartnerName: [{ required: true, message: '請输入公司名稱' }],
            // ecooperationpartnerName: [{ required: true, message: '請输入合作夥伴公司名稱'}],
            // epartnerPartnerName: [{ required: true, message: '請输入所属公司名稱' }],
            partnerIndustry: [{ required: true, message: '請输入行業' }],
            partnerRegion: [{ required: true, message: '請输入業務主要區域'}],
            partnerProduct: [{ required: true, message: '請输入主營產品/業務/服務'}],
            name: [{ required: true, message: '請输入姓名'}],
            gender: [{ required: true, message: '請输入性別'}],
            phoneNumber: [{required: true,  trigger: 'change'},{validator: checkTel}],
        },
        // 上传文件列表
        fileList: [],
        // 上传地址
        uploadApi: '',
        // 其他数据
        otherData: null,
        postparam : {
            ecooperation:{  },
            epartner : {},
            eccontacts: {},
            userInfo:{},
            file:null,
        },
        isDisable: false, 
        }
    },
    components: {
    
    },
    mounted () {
     
    },
    methods: {
    // 删除文件钩子
    fileRemvoeFunc (file, fl) {
      // this.fileList = fl
      fl.length === 0 && (this.tipFlag = true)
    },
    // 文件成功时钩子
    uploadCtrlFunc (res, file, fl) {
        this.fileList = fl
        this.loading = false
        this.$emit('confirm')
        // this.done()
        this.$message({
            type: 'success',
            message: '文件发布成功'
        })
        this.$refs.upload.clearFiles()
    },
    // 文件失败时钩子
    fileErrorFunc (err, file, fileList) {
        this.loading = false
        console.log('error: ', err, file, fileList)
        this.$message({
            type: 'error',
            message: '文件发布失败'
        })
    },
    // 上传前增加loading
    beforeUploadFunc (file) {
        if (file) {
            this.loading = true
            let fileLimitSize = 30
            if (file.size) {
                const limitSize = file.size / 1024 / 1024 < fileLimitSize
                // const limitSize
                if (!limitSize)  {
                    // debugger
                  this.$message.warning(`上传文件大小不能超过 ${fileLimitSize} MB!`)
                }
                return  limitSize && fileLimitSize
            }
        }
    },

    // 上传文件数量超出的钩子
    handleExceed (file, fileList) {
        this.$message({
            message: '请勿多次上传',
            type: 'warning'
        })
    },
    // 文件上传到浏览器的钩子
    handleUploadChange (file, fl) {
        fl.length > 0 && (this.tipFlag = false)
      // this.fileList = JSON.parse(JSON.stringify(fl))
    },
    // 附件和參數
    uploadSectionFile(file){
        this.postparams.append('file', file.file);
    },
    addCompany(FormModel) {
        this.isDisable = true
        let postparam = {
            // let params = {
            ecooperation : {
                partnerName:this.FormModel.epartnerpartnerName,
                partnerCallintime:this.FormModel.partnerCallintime,
                partnerBdOwner:this.FormModel.partnerBdOwner,
                partnerCostage:this.FormModel.partnerCostage,
                signContract:this.FormModel.signContract,
                contractDate:this.FormModel.contractDate,
                entrust:this.FormModel.entrust,
                entrustName:this.FormModel.entrustName,
                partnerAwarding:this.FormModel.partnerAwarding,
                projectName:this.FormModel.projectName,
                coType:this.FormModel.coType,
                coProgress:this.FormModel.coProgress,
                fiiCodepartment:this.FormModel.fiiCodepartment,
                },
            epartner : {
                partnerName:this.FormModel.epartnerpartnerName,
                partnerIndustry:this.FormModel.partnerIndustry,
                partnerRegion:this.FormModel.partnerRegion,
                partnerProduct:this.FormModel.partnerProduct,
                partnerList:this.FormModel.partnerList,
                partnerScale:this.FormModel.partnerScale,
                partnerAddr:this.FormModel.partnerAddr,
                partnerWebddr:this.FormModel.partnerWebddr,
                partnerTech:this.FormModel.partnerTech,
                partnerDate:this.FormModel.partnerDate,
                partnerChannel:this.FormModel.partnerChannel,
                partnerTurnover:this.FormModel.partnerTurnover,
                partnerRegistcapital:this.FormModel.partnerRegistcapital,
            },
            eccontacts : {
                name:this.FormModel.name,
                gender:this.FormModel.gender,
                phoneNumber:this.FormModel.phoneNumber,
                partnerName:this.FormModel.epartnerpartnerName,
                emailAddress:this.FormModel.emailAddress,
                title:this.FormModel.title,
            },
            userInfo : {
                userName:localStorage.getItem('USERNAME'),
                userCode:localStorage.getItem('USERCODE')
            },
        }  
        // 附件和參數
        this.postparams = new FormData()
        this.postparams.append('ecooperation',JSON.stringify(postparam.ecooperation));
        this.postparams.append('epartner', JSON.stringify(postparam.epartner));
      
        this.postparams.append('eccontacts', JSON.stringify(postparam.eccontacts));
        this.postparams.append('userInfo', JSON.stringify(postparam.userInfo));

        this.$refs[FormModel].validate(valid => {
        if (valid) {
            this.$refs.upload.submit()
            this.$store.dispatch('addCompany',this.postparams).then((rep) => {
                if(rep.data.code == 0){
                    alert('新增成功')
                    setTimeout(() => {
                        this.isDisable = false
                    }, 1000)
                    this.FormModel = {};
                    this.$refs.upload.clearFiles()//清除文件列表
                }else {
                    alert(rep.data.message)
                    this.isDisable = false
                    return false;
                }
            });
            
        } else {
            console.log("error submit!!");
            alert("有必填信息未填写，请检查！")
            this.isDisable = false
            return false;
        }
      })
    
    },

    // <!--返回-->
    gotoLogin() {
        this.$router.go(-1);
    },
    // 验证手機
    checkMobile(str) {
        let re = /^1\d{10}$/
        if (re.test(str)) {
            return true;
        }else {
            return false;
        }
    },

    }
}
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.register-l{
    float: left;
    width: 95%;
    margin: 30px;
}
.el-input{
    width: 100%;
}
.el-select{
    width: 100%;
}

</style>


