<template>    
    <div v-if="this.secName">
        <div class="topspan">
            <img src="../../../../assets/images/biaoti.png"/>
            <span>{{this.firName}} >> {{this.secName}}</span>
        </div>
    
        <div class="emstable-wrap register" >
            <div class="search-bar" >
                <!-- <div class="search-form" > -->
                    <i-layout :toolbar="bottomButton" :title="title" destroyedWhenClose size="large">
                    <i-form @reset="reset" @search="search">
                        <el-form-item label="合作夥伴公司名稱" label-width="120px">
                            <el-input v-model="searchCondition.partnerName"></el-input>
                        </el-form-item>

                        <el-form-item label="行業">
                            <el-input v-model="searchCondition.partnerIndustry"></el-input>
                        </el-form-item>
                        <el-form-item label="業務主要區域">
                            <el-input  v-model="searchCondition.partnerRegion"></el-input>
                        </el-form-item>

                        <el-form-item label="主營產品/業務/服務" label-width="120px">
                            <el-input v-model="searchCondition.partnerProduct"></el-input>
                        </el-form-item>
                    </i-form>
                  <el-button @click="exportExcel" id="exportCrew" v-if="secName == '查詢客戶信息'">导出</el-button>
                    <i-table :table="table" @pageSizeChange="handleSizeChange" @pageIndexChange="handleCurrentChange" @Mounted="componentMounted">
                        <template slot="table" slot-scope="tableScope">
                        <el-table
                            class="testFather"
                            ref="multipleTable"
                            :data="table.data"      
                            style="width: 100%"
                            highlight-current-row
                            @current-change="handleChange22"
                            @selection-change="handleChange"
                            >
                            <el-table-column label="序号" width="50">
                                <template slot-scope="scope">{{scope.$index + 1}}</template>
                            </el-table-column>
                            <el-table-column v-for="(item, idx) in table.columns"
                            :key="idx"
                            :prop="item.prop"
                            :url="item.url"
                            :label="item.label"
                            :min-width="item.width"
                            :formatter="tableScope.formatter"
                            :align="item.align"
                            >
                            </el-table-column>

                            <el-table-column label="業務主要區域" width="300px" prop="partnerRegion" align="center">
                                <template slot-scope="scope">
                                    <el-popover trigger="hover" placement="top" :visible-arrow="false">
                                    <p class="popover-p">{{ scope.row.partnerRegion }}</p>
                                    <div slot="reference" class="name-wrapper">
                                        <el-tag size="medium">
                                            {{ scope.row.partnerRegion}}
                                        </el-tag>
                                    </div>
                                    </el-popover>
                                </template>
                            </el-table-column>

                            <el-table-column label="主營產品/業務/服務" width="300px" prop="partnerProduct" align="center">
                                <template slot-scope="scope">
                                    <el-popover trigger="hover" placement="top" :visible-arrow="false">
                                    <p class="popover-p">{{ scope.row.partnerProduct }}</p>
                                    <div slot="reference" class="name-wrapper">
                                        <el-tag size="medium">
                                            {{ scope.row.partnerProduct}}
                                        </el-tag>
                                    </div>
                                    </el-popover>
                                </template>
                            </el-table-column>
                            <!-- v-if="UPDATE == 'update'" -->
                            <el-table-column
                                label="操作"
                                fixed="right"
                                align="center"
                                min-width="50"
                                show-overflow-tooltip
                                >
                                <template slot-scope="scope">
                                    <el-button 
                                    @click.native.prevent="updateCompany(scope.row.partnerNo)"
                                    type="text"
                                    size="small"
                                    
                                    >修改</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        </template>
                    </i-table>
                    </i-layout>

                </div>
            <!-- </div> -->
        </div>
 </div>  
</template>
<script> 
import config from '@/store/common/actions'
import { BASE_URL } from '@/store/api'
export default {
    data(){
      return {   
        firName:localStorage.getItem('FIRNAME'),
        secName:localStorage.getItem('SECNAME'),
        partnerNo: localStorage.getItem('PARTNERNO'),
        UPDATE:JSON.parse(localStorage.getItem('LIMITS')).UPDATE,
        // 顶部查询条件
        searchCondition: {
            partnerName: '',
            partnerIndustry: '',
            partnerRegion: '',
            partnerProduct:''
        },
        rep:[],
        partnerNoSelect:[],
        table: {
            columns: [
            // { prop:$index, label: '序号', width: '100', align: 'center' },//, func: this.seeCompany
            { prop:'partnerName', label: '公司名稱', width: 200, align: 'center',key: 'partnerNo',
                // render: (h, params) => {
                // let url = 'http://localhost:8080/#/customerDetails?partnerNo='+params.row.partnerNo
                // return h('div', {}, [
                //     h('a', { 
                //         attrs: {
                //             href: url,
                //             target: '_black'
                //         }
                //     })
                //     ], params.row.partnerNo)
                // }
            },
            { prop:'partnerIndustry', label: '行業', width: 200, align: 'center' },
            // { prop:'partnerRegion', label: '業務主要區域', width: 200, align: 'center' },
            // { prop:'partnerProduct', label: '主營產品/業務/服務', width: 200, align: 'center' },
            ],
           
            search: null,
            data: null,
            // 注册 page 相关信息
            // ---是否显示底部分页
            showPagebar: true,
            info: null,
            totalSize: 0,
            
            }
            }
        },
    components: {
    //   Home
    },
    mounted () {
     
    },
    methods: {
    // 监听每页显示条数变化
    handleSizeChange (val) {
        let params = {
            "pageRequest":{
                pageIndex: val.pageIndex,
                pageSize: val.pageSize,
            },
            "epartner" : {
                partnerName: this.searchCondition.partnerName,
                partnerIndustry: this.searchCondition.partnerIndustry,
                partnerRegion: this.searchCondition.partnerRegion,
                partnerProduct: this.searchCondition.partnerProduct,
            },
            userInfo:{
                userCode:localStorage.getItem('USERCODE')
            },
        } 
        this.table.info = val
        this.getData(params)
    },
    // 监听页数变化
    handleCurrentChange (val) {
        let params = {
            "pageRequest":{
                pageIndex: val.pageIndex,
                pageSize: val.pageSize,
            },
            "epartner" : {
                partnerName: this.searchCondition.partnerName,
                partnerIndustry: this.searchCondition.partnerIndustry,
                partnerRegion: this.searchCondition.partnerRegion,
                partnerProduct: this.searchCondition.partnerProduct,
            },
            userInfo:{
                userCode:localStorage.getItem('USERCODE')
            },
        } 
        this.table.info = val
        this.getData(params)
    },
    // 获取表格数据
    getData (val) {
        let params = {
            "pageRequest":{
                pageIndex:val.pageRequest.pageIndex,
                pageSize:val.pageRequest.pageSize,
            },
            "epartner" : {
                partnerName: this.searchCondition.partnerName,
                partnerIndustry: this.searchCondition.partnerIndustry,
                partnerRegion: this.searchCondition.partnerRegion,
                partnerProduct: this.searchCondition.partnerProduct,
            },
            userInfo:{
                userCode:localStorage.getItem('USERCODE')
            },
        } 
        this.$store.dispatch('getCompanyData', params)
        .then(res => {
            if(res.code == 0){
                // let rep = res.data.content
                this.rep = res.data.content
                Object.keys(res).map(key => (this.table[key] = res.data[key]))
                this.table.data = this.rep
                this.table.totalSize = res.data.totalSize
                this.table.totalPages = res.data.totalPages
                // console.log(this.table.totalPages)
            } 
            
        })
    },
    updateCompany(partnerNo){
        localStorage.setItem('PARTNERNO', partnerNo);
        this.$store.dispatch('getEcooperationList', {partnerNo:partnerNo})
        .then(res => {
            if(res.data.code == 0){
                // window.open("http://localhost:8080/#/customerUpdate?partnerNo="+partnerNo)
                window.open(`${BASE_URL}/#/customerUpdate?partnerNo=`+partnerNo)
            }
        })
    },
    // 组件加载好了之后再去触发table.search的变化
    componentMounted () {
      this.table.search = {...this.searchCondition}
    },
    // 查询
    search () {
        this.table.search = { ...this.searchCondition }
    },
    // 重置
    reset () {
        Object.keys(this.searchCondition).map(item => {
            this.searchCondition[item] = ''
        })
        this.table.search = { ...this.searchCondition }
    },
    handleChange (val) {
        // localStorage.removeItem('PARTNERNO');
        // this.partnerNoSelect = val.map(item => {
        //     return item.partnerNo
        // })
        // localStorage.setItem('PARTNERNO', this.partnerNoSelect[0]);
    },

}
}

</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.el-form-item {
    width: 50%;
}
.testFather {
    // background: black;
    table {
        thead {
            tr {
                th:nth-child(1) {
                    display: none !important;
                }
            }
        }
    }
}
.popover-p{
    margin:3px;
    width:200px;
    height:auto;
}

</style>


