<template>    
    <div v-if="this.secName">
        <div class="topspan">
            <img src="../../../../assets/images/biaoti.png"/>
            <span>{{this.firName}} >> {{this.secName}}</span>
        </div>
    
        <div class="emstable-wrap register" >
            <div class="search-bar" >
                <!-- <div class="search-form" > -->
                    <i-layout :toolbar="bottomButton" :title="title" destroyedWhenClose size="large">
                    <i-form @reset="reset" @search="search">
                        <el-form-item label="合作夥伴公司名稱" label-width="120px">
                            <el-input v-model="searchCondition.partnerName"></el-input>
                        </el-form-item>

                        <el-form-item label="行業">
                            <el-input v-model="searchCondition.partnerIndustry"></el-input>
                        </el-form-item>
                        <el-form-item label="業務主要區域">
                            <el-input  v-model="searchCondition.partnerRegion"></el-input>
                        </el-form-item>

                        <el-form-item label="主營產品/業務/服務" label-width="120px">
                            <el-input v-model="searchCondition.partnerProduct"></el-input>
                        </el-form-item>
                    </i-form>
                    <i-table :table="table" @pageSizeChange="handleSizeChange" @pageIndexChange="handleCurrentChange" @Mounted="componentMounted">
                        <template slot="table" slot-scope="tableScope">
                        <el-table
                            class="testFather"
                            ref="multipleTable"
                            :data="table.data"      
                            style="width: 100%"
                            highlight-current-row
                            @current-change="currentRow"
                            @selection-change="handleChange"
                        >
                            <el-table-column type="selection" width="55"></el-table-column>
                            <el-table-column label="序号" width="50">
                                <template slot-scope="scope">{{scope.$index + 1}}</template>
                            </el-table-column>
                            <el-table-column v-for="(item, idx) in table.columns"
                            :key="idx"
                            :prop="item.prop"
                            :label="item.label"
                            :min-width="item.width"
                            :formatter="tableScope.formatter"
                            :align="item.align"
                            >
                            </el-table-column>
                            <el-table-column label="業務主要區域" width="300px" prop="partnerRegion" align="center">
                                <template slot-scope="scope">
                                    <el-popover trigger="hover" placement="top" :visible-arrow="false">
                                    <p class="popover-p">{{ scope.row.partnerRegion }}</p>
                                    <div slot="reference" class="name-wrapper">
                                        <el-tag size="medium">
                                            {{ scope.row.partnerRegion}}
                                        </el-tag>
                                    </div>
                                    </el-popover>
                                </template>
                            </el-table-column>

                            <el-table-column label="主營產品/業務/服務" width="300px" prop="partnerProduct" align="center">
                                <template slot-scope="scope">
                                    <el-popover trigger="hover" placement="top" :visible-arrow="false">
                                    <p class="popover-p">{{ scope.row.partnerProduct }}</p>
                                    <div slot="reference" class="name-wrapper">
                                        <el-tag size="medium">
                                            {{ scope.row.partnerProduct}}
                                        </el-tag>
                                    </div>
                                    </el-popover>
                                </template>
                            </el-table-column>

                            <el-table-column
                                v-if="tableScope.operator"
                                label="操作"
                                fixed="right"
                                align="center"
                                min-width="90"
                                show-overflow-tooltip>
                                <template slot-scope="scope">
                                <i-table-operator
                                    :table="table"
                                    :row="scope.row"
                                />
                                </template>
                            </el-table-column>
                            <el-table-column
                                label="操作"
                                fixed="right"
                                align="center"
                                min-width="80"
                                show-overflow-tooltip>
                                <template slot-scope="scope">
                                    <el-button v-if="scope.row.status == 0 && POWER == 'power'"
                                    @click.native.prevent="hideRow(scope.row.status, scope.row.partnerNo)"
                                    type="text"
                                    size="small">隐藏</el-button>

                                    <el-button v-if="scope.row.status == 1 && POWER == 'power'"
                                    @click.native.prevent="cancelHideRow(scope.row.status, scope.row.partnerNo)"
                                    type="text"
                                    size="small">取消隐藏</el-button>
                                </template>
                            </el-table-column>

                        </el-table>
                        </template>
                    </i-table>
                    </i-layout>
                </div>
            <!-- </div> -->
        </div>
 </div>  
</template>
<script> 
import ITable from '@/components/common/i-table'
import IafSearchMixin from '@/mixins/compensation/iaf-search-mixin'
import ITableOperator from 'components/common/i-table-operator'
export default {
    components: {
       ITable,
       ITableOperator,
    },
    mixins: [IafSearchMixin],
    data(){
      return {
        firName:localStorage.getItem('FIRNAME'),
        secName:localStorage.getItem('SECNAME'),
        POWER:JSON.parse(localStorage.getItem('LIMITS')).POWER,
        // 顶部查询条件
        searchCondition: {
            partnerName: '',
            partnerIndustry: '',
            partnerRegion: '',
            partnerProduct:''
        },
        deleteArr:[],
        // 表格列表配置项
        table: {
            columns: [
            { prop:'partnerName', label: '公司名稱', width: 200, align: 'center'},
            { prop:'partnerIndustry', label: '行業', width: 200, align: 'center' },
            // { prop:'partnerRegion', label: '業務主要區域', width: 200, align: 'center' },
            // { prop:'partnerProduct', label: '主營產品/業務/服務', width: 200, align: 'center' },

            ],
            search: null,
            data: null,
            showPagebar: true,
            info: null,
            totalSize: 0,
            multiple: true,
            status:0,

            setting: {
                toolbar: [{
                    text: '新增',
                    func: this.addRecord,
                },
                {
                    func: this.deleteRecord,
                    text: '删除',
                    type: 'danger',
                    show: row => {
                        let status = JSON.parse(localStorage.getItem('LIMITS')).DEL == 'del'
                        return { empty: !status }
                    }
                    // func: vm => {
                    //     vm.deleteData()
                    // },
                }],
            },
            }
            }
        },
    components: {
    //   Home
    },
    mounted () {
     
    },
    methods: {
    // 监听每页显示条数变化
    handleSizeChange (val) {
        let params = {
            "pageRequest":{
                pageIndex: val.pageIndex,
                pageSize: val.pageSize,
            },
            "epartner" : {
                partnerName: this.searchCondition.partnerName,
                partnerIndustry: this.searchCondition.partnerIndustry,
                partnerRegion: this.searchCondition.partnerRegion,
                partnerProduct: this.searchCondition.partnerProduct,
            },
            userInfo:{
                userCode:localStorage.getItem('USERCODE')
            },
        } 
        this.table.info = val
        this.getData(params)
    },
    // 监听页数变化
    handleCurrentChange (val) {
        let params = {
            "pageRequest":{
                pageIndex: val.pageIndex,
                pageSize: val.pageSize,
            },
            "epartner" : {
                partnerName: this.searchCondition.partnerName,
                partnerIndustry: this.searchCondition.partnerIndustry,
                partnerRegion: this.searchCondition.partnerRegion,
                partnerProduct: this.searchCondition.partnerProduct,
            },
            userInfo:{
                userCode:localStorage.getItem('USERCODE')
            },
        } 
        this.table.info = val
        this.getData(params)
    },
    // 获取表格数据
    getData (val) {
        let params = {
            pageRequest:{
                pageIndex:val.pageRequest.pageIndex,
                pageSize:val.pageRequest.pageSize,
            },
            epartner : {
                partnerName: this.searchCondition.partnerName,
                partnerIndustry: this.searchCondition.partnerIndustry,
                partnerRegion: this.searchCondition.partnerRegion,
                partnerProduct: this.searchCondition.partnerProduct,
            },
            userInfo:{
                userCode:localStorage.getItem('USERCODE')
            },
        } 
        this.$store.dispatch('getCompanyData', params)
            .then(res => {
                if(res.code == 0){
                    let rep = res.data.content

                    Object.keys(res).map(key => (this.table[key] = res.data[key]))
                    this.table.data = rep
                    this.table.totalSize = res.data.totalSize
                    this.table.totalPages = res.data.totalPages
                } 
                
            })
        },
        componentMounted () {
            this.table.search = {...this.searchCondition}
        },
        // 查询
        search () {
            this.table.search = { ...this.searchCondition }
        },
        // 重置
        reset () {
            Object.keys(this.searchCondition).map(item => {
                this.searchCondition[item] = ''
            })
            this.table.search = { ...this.searchCondition }
        },
        // 新增公司新增客戶信息
        addRecord () {
            this.$router.replace({
                path: "/addCustomer",
                name:'新增客戶信息',
            })
            // localStorage.setItem('SECNAME', '新增客戶信息');
        },
        // 批量删除记录
        deleteRecord () {
            // console.log(this.deleteArr)
            if (this.deleteArr.length === 0) {
                this.$message({
                type: 'info',
                message: '请至少选择一条记录'
                })
            } else {
                this.$confirm('确认删除？').then(() => {
                    this.$store.dispatch('delCompany', {data:this.deleteArr}).then(rep => {
                    // console.log(rep)
                    if (rep.data.code == 0) {
                        this.$message({
                            type: 'success',
                            message: '删除成功'
                        })
                        this.table.search = { ...this.searchCondition }
                    } else {
                        this.$message({
                            type: 'error',
                            message: '删除失败'
                        })
                    }
                })
                }).catch(() => {})
            }
        },
        handleChange (val) {
            this.deleteArr = val.map(item => {
                return item.partnerNo
            })
        },
        //隱藏
        hideRow(status, partnerNo){
            this.$confirm('确认隱藏該公司？').then(() => {

                this.$store.dispatch('hideCompany', {partnerNo:partnerNo,status:1})
                .then(res => {
                    alert("隱藏成功")
                    this.table.search = { ...this.searchCondition }
                })
            })
        },
        //取消隱藏
        cancelHideRow(status, partnerNo){
            this.$confirm('确认取消隱藏該公司？').then(() => {

                this.$store.dispatch('hideCompany', {partnerNo:partnerNo,status:0})
                .then(res => {
                    alert("取消隱藏成功")
                    this.table.search = { ...this.searchCondition }
                })
            })
        }
    }
}
</script>
<style lang="scss" scoped>
@import "../../../../assets/css/base-table.less";
.el-form-item {
    width: 50%;
}
.popover-p{
    margin:3px;
    width:200px;
    height:auto;
}
</style>


